<template>
  <div>

    <div v-for="(macchina,index) in macchine" :key="index">
      <h1>{{macchina.nome}}</h1>
      <div v-for="(stallo,index) in macchina.stalli" :key="index">{{stallo.nome}}</div>
    </div>
  </div>
</template>

<script>
import apiCiclatura from './../api/ciclatura.js'
export default {
  name: 'ListaMacchine',
  data () {
    return {
      macchine: []
    }
  },
  created () {
    this.loadDati()
  },
  methods: {
    loadDati: function () {
      console.log('Laod dati')
      let dati = apiCiclatura.getMacchine()
      this.macchine = dati
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



