import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Ciclatura from '@/components/Ciclatura'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/ciclature',
      name: 'Ciclatura',
      component: Ciclatura
    }
  ]
})



